import * as React from 'react'
import { graphql } from 'gatsby'

import Page from '../components/Page'
import IndexLayout from '../layouts'
import styled from '@emotion/styled'
import Card from '../components/Card'
import StyledContainer from '../components/StyledContainer'
import SEO from '../components/SEO'
import { Disqus, CommentCount } from 'gatsby-plugin-disqus'


const BlogContent = styled.div`
`;

interface BlogPostTemplateProps {
  data: {
    site: {
      siteMetadata: {
        title: string
        siteUrl: string
        description: string
        author: {
          name: string
          url: string
        }
      }
    }
    markdownRemark: {
      html: string
      excerpt: string
      frontmatter: {
        title: string
        headerImg: any
        postedAt: {
          name: string
          url: string
        }
      }
    }
  }
}

const BlogPostTemplate: React.FC<BlogPostTemplateProps> = ({ data }) => {

  let disqusConfig = {
    url: `${data.site.siteMetadata.siteUrl + location.pathname}`,
    identifier: data.markdownRemark.frontmatter.title,
    title: data.markdownRemark.frontmatter.title,
  }


  return (
    <IndexLayout>
      <Page>
        <SEO title={data.markdownRemark.frontmatter.title} description={data.markdownRemark.excerpt} imageSrc={data.markdownRemark.frontmatter.headerImg.childImageSharp.fluid.imageSrc} />
        <StyledContainer>
          <Card card={data.markdownRemark}>
            {/* eslint-disable-next-line react/no-danger */}
            <BlogContent className="BlogContent" dangerouslySetInnerHTML={{ __html: data.markdownRemark.html }} />
          </Card>
          <Disqus config={disqusConfig} />

        </StyledContainer>
      </Page>
    </IndexLayout>
  )
}

export default BlogPostTemplate

export const query = graphql`
  query BlogPostTemplateQuery($slug: String!) {
    site {
      siteMetadata {
        title
        description
        siteUrl
        author {
          name
          url
        }
      }
    }
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      excerpt(pruneLength: 380)
      timeToRead
      frontmatter {
        postedAt {
          name
          url
        }
        title
          date
          tags
          headerImg {
            childImageSharp {
              fluid(maxWidth:960, maxHeight:360) {
                ...GatsbyImageSharpFluid
              }
            }
          }
      }
    }
  }
`

